import { Card, CardContent, Container, Grid, Typography } from "@mui/material";
import { useMiddleware, useTask } from "../../../../Application";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";
import useFetch from "../../../../utils/useFetch";
import pricify from "../../../../utils/pricify";

const IncomeExpenseReport = () => {
    const middleware = useMiddleware('Transactions.Reports');

    const { data, loading, reload } = useFetch({
        handler: () => middleware.incomeExpense(),
    });


    const stats = [
        {
            label: 'درآمد',
            value: () => data?.income ? <>{pricify(data.income)} <small>ریال</small></> : '-'
        },
        {
            label: 'هزینه',
            value: () => data?.expense ? <>{pricify(data.expense)} <small>ریال</small></> : '-'
        },
    ]

    const task = useTask('transactions.incomeExpenseReport');


    return (
        <TabView {...task}>
            <ResponsiveContainer fullHeight>
                <Container maxWidth="sm" disableGutters>
                    <Card
                        variant="outlined"
                        sx={{
                            flexGrow: 1,
                            display: 'flex',
                            flexDirection: 'column'
                        }}
                    >
                        <CardContent sx={{ p: 6 }}>
                            <Grid container spacing={2}>
                                {stats.map((stat, index) => (
                                    <Grid item xs={12} sm={6} textAlign="center" key={index}>
                                        <Typography variant="h5" fontWeight={800}>{typeof stat.value == "function" ? stat.value() : stat.value}</Typography>
                                        <Typography variant="h6">{stat.label}</Typography>
                                    </Grid>
                                ))}
                            </Grid>
                        </CardContent>
                    </Card>
                </Container>
            </ResponsiveContainer>
        </TabView>
    )
}

export default IncomeExpenseReport;